import {  
    mapMutations  
} from 'vuex';
export default{
	data(){
		return {
			headerPosition:"fixed",
			headerTop:"0px",
			accountBtnSee:false,
			passBtnSee:false,
			user:{
				account:"",
				password:""
			},
			isActive:false
		}
	},
	onLoad(options){



	},
	methods:{
		...mapMutations(['login']),
		inputChange(e){
			var val = e.detail.value;
			if(typeof(val) != "undefined" && val != ""){
				var id = e.currentTarget.id;
				if("account" == id){
					this.accountBtnSee = true;
				}else{
					this.passBtnSee = true;
				}
				//登录按钮开关
				if(this.user.account != "" && this.user.password != ""){
					this.isActive = true;
				}else{
					this.isActive = false;
				}
			}else{
				this.accountBtnSee = false;
				this.passBtnSee = false;
				this.isActive = false;
			}
		},
		clearInput(e){
			var id = e.currentTarget.id;
			if(typeof(id) != "undefined" && id != ""){
				if("accountClear" == id){
					this.user.account = "";
					this.accountBtnSee = false;
				}else{
					this.user.password = "";
					this.passBtnSee = false;
				}
				this.isActive = false;
			}
		},
		async toLogin(){
			if(this.isActive){
				this.logining = true;
			}else{
				return;
			}
			/* 数据验证模块
			if(!this.$api.match({
				mobile,
				password
			})){
				this.logining = false;
				return;
			}
			*/
			const result = await this.$api.json('userInfo');
			if(result.status === 1){
				this.login(result.data);
		        uni.navigateBack();  
			}else{
				this.$api.msg(result.msg);
				this.logining = false;
			}
		}
	}
}
